<template>
    <div class="mine">
        <img class="mineHbg" src="@/assets/image/bg1.png" alt="">
        <div class="mine_header">
            <img src="@/assets/image/header1.png" alt="">
            <p>ping</p>
        </div>
        <div class="mine_list">
            <!-- <mt-cell title="后台管理" to="##" is-link value=""></mt-cell> -->
            <ul>
                <li v-for="item in mineList">
                    <router-link :to="{path:item.pathUrl,query:item.params}"><span>{{item.names}}</span><img src="@/assets/image/right2.png" alt=""></router-link>
                </li>
                <!-- <li>
                    <router-link :to="{path:'/mineset'}"><span>设置</span><img src="@/assets/image/right2.png" alt=""></router-link>
                </li> -->
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:'mine',
    data(){
        return{
            backgroundImg: 'url('+require('@/assets/image/right2.png')+') no-repeat center center/contain',
            mineList:[
                {pathUrl:'/admin',names:'后台管理',params:''},
                {pathUrl:'/mineset',names:'设置',params:''},
                {pathUrl:'/iframes_mine',names:'我的Git',params:{iframeUrl:'https://github.com/Jping0'}},
                {pathUrl:'/iframes_mine',names:'公司项目管理系统',params:{iframeUrl:'http://oms.iswweb.com'}},
                {pathUrl:'/iframes_mine',names:'码云Gitee',params:{iframeUrl:'https://www.gitee.cn'}},
            ]
        }
    }
}
</script>
<style>
    .mine{
        padding-top: 40px;
        position: relative;
    }
    .mineHbg{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: auto;
    }
    .mine_header{
        text-align: center;
        position: relative;
        z-index: 3;
    }
    .mine_header img{
        object-fit: cover;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
        box-shadow: 0px 0px 10px 0px rgb(170, 170, 170);
    }
    .mine_header p{
        font-size: 16px;
        margin-top: 10px;
        color: white;
    }
    .mine_list{
        margin-top: 60px;
    }
    .mine_list li{
        border-bottom: 1px solid rgb(230, 230, 230);
    }
    .mine_list li a{
        display: block;
        line-height: 50px;
        padding: 0px 16px;
        position: relative;
    }
    .mine_list li a img{
        position: absolute;
        top: 50%;
        right: 16px;
        transform: translateY(-50%);
        display: inline-block;
        width: 16px;
        height: 16px;
    }
</style>


